.root {
  position: relative;
  width: 100%;
  text-align: center;
  margin-bottom: 60px;
  z-index: 6;
}

.input {
  border: 1px solid rgba(3, 105, 161, 0.1);
  padding: 12px 20px;
  padding-left: 42px;
  border-radius: 10px;
  font-size: 16px;
  width: 100%;
  caret-color: rgba(3, 105, 161, 0.5);
}

input:focus {
  border: 1px solid rgba(3, 105, 161, 0.2);
  outline: none;
}

.icon {
  width: 22px;
  height: 22px;
  opacity: 0.3;
  position: absolute;
  left: 15px;
  top: 12px;
}

.clear {
  width: 18px;
  height: 18px;
  opacity: 0.3;
  position: absolute;
  right: 15px;
  top: 15px;
  cursor: pointer;
}

.clear:hover {
  opacity: 0.8;
}

.searchbar {
  width: 100%;
  height: auto;
  background-color: #fff;
  position: absolute;
  z-index: 0;
  left: 0px;
  top: 42px;
  border: 1px solid rgba(3, 105, 161, 0.2);
  border-top: none;
  border-radius: 0px 0px 10px 10px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.searchcircle {
  width: 30px;
  height: 30px;
}

.city:hover {
  color: #fff;
  background-color: #075985;
}

@media not all and (min-width: 768px) {
  .root {
    margin-bottom: 20px;
  }

  .input {
    font-size: 14px;
  }

  .icon {
    width: 20px;
    height: 20px;
  }

  .searchcircle {
    width: 24px;
    height: 24px;
  }

  .searchbar {
    font-size: 14px;
    padding-top: 10px;
    padding-bottom: 10px;
    top: 40px;
  }
}
